
import { defineComponent } from 'vue';

const components: Array<any> = require('@/components/index.json');
const sidebar = Object.values(components).map(item => {
  return {
    path: `/components/${item.name}`,
    title: item.title,
  };
});

export default defineComponent({
  name: 'Layout',
  setup() {
    return {
      sidebar,
    };
  },
});
